@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
    margin: 0;
    font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    text-decoration: none;
}
/* HEADER START */

.header a {
    font-size: 13px;
    margin-right: 30px;
    font-weight: 100;
    text-transform: uppercase;
    color: black;
}

.header {
    padding: 4vh 3vh;
}

.banner__img {
    object-fit: contain;
    width: 50vw;
    min-width: 500px;
    -webkit-filter: grayscale(100%);
    /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
    padding: 0 1vw;
}

.banner {
    width: 100vw;
    display: flex;
    min-height: 89vh;
    justify-content: flex-start;
    align-items: flex-end;
}

.banner__info {
    margin: 10vw;
    align-self: center;
}

.banner__info p {
    font-family: monospace;
}

.banner__heading {
    font-size: 50px;
}

@media (max-width: 831px) {
    .banner {
        flex-direction: column-reverse;
        align-items: center;
    }
    .banner__img {
        width: 100%;
        min-width: 0;
    }
}


/* HEADER END */


/* ABOUT START */

.about {
    width: 100vw;
    margin: 20vh 0px;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about__left {
    padding: 5%;
    flex: 0.5 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    height: 100%;
}

.about__right {
    flex: 0.5 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about__left p {
    font-size: 25px;
    width: 100%;
}

.card__top {
    height: 20%;
    min-height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    background-position: center;
    background-size: cover;
}

.card__top p {
    color: rgb(173, 173, 173);
    font-size: 10px;
}

.about__card {
    margin: auto;
    min-height: 800px;
    height: 95%;
    max-width: 400px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    color: black;
    box-shadow: 11px 11px 25px #b0b0b0, -11px -11px 25px #ffffff;
}

.card__middle {
    margin-top: 15%;
    font-size: 17px;
    padding: 20px 30px;
}

.card__middle h3 {
    text-align: center;
    font-size: 25px;
}

.about__circle h4 {
    font-size: 13px;
}

.about__icons {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: auto;
}

.about__icons img {
    width: 100%;
    max-height: 120px;
    object-fit: contain;
}

.about__circle {
    /* box-shadow: 5px 5px 8px -5px rgba(0, 0, 0, 0.5); */
    width: 45%;
    padding: 10px 5px;
    border-radius: 30px;
    /* background: linear-gradient(145deg, #e6e6e6, #ffffff);
    box-shadow: 16px 16px 32px #bdbdbd, -16px -20px 32px #ffffff; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 5px;
}

.card__tech {
    cursor: pointer;
    transition: all 500ms;
}

.card__bottom {
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    justify-content: center;
}

.card__bottom--get {
    font-size: 15px;
    padding: 20px 2%;
}

.card__bottom--listItem {
    font-size: 13px;
    padding: 5px 0px;
    color: rgb(184, 184, 184);
    font-family: sans-serif;
    font-weight: 600;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.card__bottom--list {
    display: flex;
    padding: 20px;
    flex-direction: column;
}

@media (max-width: 831px) {
    .about {
        flex-direction: column;
    }
}


/* ABOUT END */

.project__heading {
    font-size: 50px;
    color: rgb(77, 77, 77);
    text-transform: uppercase;
    margin: 10px 5%;
}

.project {
    margin: 10px;
    border: 1px rgb(230, 230, 230) solid;
    /* box-shadow: 10px 14px 27px -8px rgba(0, 0, 0, 0.49); */
    /* border-radius: 30px; */
    width: 45%;
    min-width: 400px;
    background-position: center;
    background-size: cover;
    transition: all 500ms;
}

.project:hover {
    transform: scale(1.01);
}

.project__shot {
    object-fit: contain;
    width: 100%;
}

.project__icon {
    width: 15%;
    object-fit: contain;
    border-radius: 99px;
    align-self: flex-end;
}

.project__wrap {
    margin: auto;
    display: flex;
    flex-direction: column;
    height: 100%;
    /* background: linear-gradient(90deg, black, transparent); */
    background-color: rgba(0, 0, 0, 0);
    /* border-radius: 30px; */
}

.project__wrap .project__top {
    padding: 10px;
    display: flex;
    align-items: center;
}

.project__shot {
    object-fit: contain;
    width: 100%;
    /* border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px; */
}

.project__info {
    margin-left: 10px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1 1;
}

.project__button {
    font-family: sans-serif;
    font-weight: 700;
    color: #4E8CF0;
    margin-right: 5px;
    background-color: rgb(241, 241, 241);
    font-size: 13px;
    padding: 5px 30px;
    border-radius: 99px;
    transition: all 500ms;
}

.project__button:hover {
    color: rgb(241, 241, 241);
    background-color: #4E8CF0;
}

.project__wrap p {
    width: 60%;
    font-size: 13px;
}

.projects {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
}

.project__des {
    font-size: 13px;
}

.project__name {
    font-size: 20px;
}

@media (max-width: 1000px) {
    .project {
        min-width: 0;
        width: 100%;
    }
    .project__des {
        font-size: 11px;
    }
    .project__name {
        font-size: 15px;
    }
}

.social {
    display: flex;
    transition: all 500ms ease;
}


