.social__circle {
    color: black;
    background-color: #e2e2e2;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    transition: all 500ms ease;
    margin: 5px;
    border: 1px transparent solid;
    font-size: 30px;
    padding: 5px;
}

.social__circle:hover {
    border: 1px white solid;
    background-color: #323232;
    color: #e2e2e2;
}

.socialButton {
    margin: 5px 0px;
}

.project__top {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px rgb(226, 226, 226) solid;
}

.projectPage {
    width: 80%;
    margin: auto;
    margin-top: 50px;
}

.projectPage__icon {
    width: 10%;
    max-width: 80px;
    min-width: 40px;
}

.btn__live {
    color: white;
    background-color: #6BD45F;
    margin-right: 0;
    padding: 10px 25px;
    border-radius: 0;
    border-top-left-radius: 99px;
    border-bottom-left-radius: 99px;
    transition: all 500ms;
}

.btn__code {
    border-radius: 0;
    border-top-right-radius: 99px;
    border-bottom-right-radius: 99px;
    color: white;
    margin-right: 0;
    padding: 10px 25px;
    background-color: #4E8CF0;
    transition: all 500ms;
}

.btn__only {
    border-radius: 99px;
    color: white;
    margin-right: 0;
    padding: 10px 50px;
    background-color: #4E8CF0;
    transition: all 500ms;
}

.projectPage__btns {
    display: flex;
    border-radius: 99px;
}

.page__shots {
    margin: 20px 0px;
    width: 100%;
    display: flex;
    overflow: scroll;
}

.page__shots img {
    margin: 0px 1px;
}

.page__phoneShot {
    width: 21%;
    object-fit: contain;
}

.page__desktopShot {
    width: 79%;
    object-fit: contain;
}

.page__description p {
    color: rgb(117, 116, 116);
    font-size: 15px;
    margin: 20px 0px;
}

.page__description h1 {
    color: gray;
    margin: 10px 0px;
}

.btn__live:hover {
    background-color: #90d488;
}

.btn__code:hover,
.btn__only:hover {
    background-color: #6d9ce9;
}


/* @media(max-width: 831px) {
    .page__shots {}
    .page__phoneShot {},
    .page__desktopShot {}
} */

@media(max-width: 500px) {
    .projectPage {
        width: 95%;
        margin-top: 10px;
    }
    .btn__code,
    .btn__live {
        font-size: 10px;
        padding: 5px 10px;
    }
    .page__shots {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .page__shots img {
        height: 100%;
    }
    .btn__only {
        font-size: 10px;
        padding: 5px 20px;
    }
}

.contact {
    margin-top: 20vh;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 5%;
    /* background-color: #3b3b3b; */
}

.contact__heading {
    font-size: 50px;
}

@media (max-width: 800px) {
    .contact {
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
}

.contactCard {
    width: 45vw;
    /* border: 1px rgb(122, 122, 122) solid; */
    display: flex;
    align-items: center;
    padding: 5vw 2vw;
    border-radius: 30px;
    background-color: #323232;
    color: white;
}

.contact__img img {
    border-radius: 100%;
    background-color: gray;
    object-fit: contain;
    width: 100%;
}

.contact__img {
    width: 40%;
}

.contact__info {
    padding: 2vw;
}

.section__heading {
    color: #2c2c2c;
    font-size: 30px;
    text-transform: uppercase;
}

@media (max-width: 800px) {
    .contactCard {
        width: 100%;
        justify-content: center;
        flex-direction: column;
        margin: auto;
    }
    .contact {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .section__heading {
        font-size: 20px;
    }
}

.tech__icon {
    object-fit: contain;
    margin: 0px 2px;
}

.project__icons {
    margin-top: 5px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
}
